<template>
    <scroll-view :refresher-background="'#ffffff'" :scroll-y="true" :refresher-triggered="isRefresherTriggered"
        :refresher-enabled="true" @refresherpulling="refresherpulling" :scroll-with-animation="true"
        :style="'height: ' + height + ';position: relative;'"
        :scroll-into-view="isToBottom && bodyData.content.length > 0 ? 'bottomInScroll' : ''">


        <view class="box box-top">
            <user-card v-if="room.typeCode == 'privateChat'" :ovoUserInfo="room.otherUser"></user-card>
        </view>
        <view class="box">

            <!-- 如果没有消息时显示 -->
            <image v-if="bodyData.content.length == 0" :src="$kt.file.byPath('image/tabbar/msg.svg')" class="box-msg-icon"
                mode="widthFix" />

            <view v-for="(item, index) in bodyData.content" class="box-msg" :style="{
                'text-align': item.isMeSend ? 'right' : 'left'
            }">

                <view v-if="item.isShowTime || index == 0" class="box-msg-time">
                    {{ $kt.util.readableDate(item.gmtCreate) }}
                </view>

                <!-- 头像 -->
                <avatar v-if="!item.isMeSend" :fileId="item.ovoUserOfSend.user.fileIdOfAvatar"></avatar>

                <view class="box-msg-content">

                    <!-- 昵称 -->
                    <view class="box-msg-content-nickname">
                        {{ item.ovoUserOfSend.user.nickname }}
                    </view>

                    <!-- 内容 -->
                    <view class="box-msg-content-val" :style="{
                        'border-radius': isMeSend(item) ? '30rpx 0rpx 30rpx 30rpx' : '0rpx 30rpx 30rpx 30rpx'
                    }">

                        <text :selectable="true" v-if="item.typeCode == 'text'">{{ item.content }}</text>
                        <image style="max-width: 450rpx;border-radius: 20rpx" :style="{
                            'border-radius': isMeSend(item) ? '30rpx 0rpx 30rpx 30rpx' : '0rpx 30rpx 30rpx 30rpx'
                        }" v-if="item.typeCode == 'image'" @click="previewImage($kt.file.visit(item.content))"
                            :src="$kt.file.visit(item.content)" mode="widthFix" />
                        <video style="max-width: 450rpx;border-radius: 20rpx" :style="{
                            'border-radius': isMeSend(item) ? '30rpx 0rpx 30rpx 30rpx' : '0rpx 30rpx 30rpx 30rpx'
                        }" v-if="item.typeCode == 'video'" :src="$kt.file.visit(item.content)" controls="controls" />

                        <!-- 语音 -->
                        <view @click="playVoice(item)" v-if="item.typeCode == 'voice'" class="box-msg-content-val-voice"
                            :style="{
                                'width': voiceToWidth(item),
                                'textAlign': isMeSend(item) ? 'right' : 'left',
                            }">
                            <view class="box-msg-content-val-voice-time"
                                v-if="item.duration != null && item.duration != '' && isMeSend(item)">
                                <view style="height: 10rpx;"></view>
                                {{ (item.duration / 1000).toFixed(0) }}{{ "\"" }}
                            </view>

                            <view v-if="!isPlayingVoice || playingVoiceItem.content != item.content"
                                style="display: inline-block;" :style="{
                                    'transform': isMeSend(item) ? 'rotate(180deg)' : 'rotate(0deg)'
                                }">
                                <image class="box-msg-content-val-voice-icon" :src="$kt.file.byPath('image/icon/voice.svg')"
                                    mode="widthFix" />
                            </view>

                            <view v-if="isPlayingVoice && playingVoiceItem.content == item.content"
                                style="display: inline-block;" :style="{
                                    'transform': isMeSend(item) ? 'rotate(180deg)' : 'rotate(0deg)'
                                }">
                                <image class="box-msg-content-val-voice-icon" v-if="voiceIconIndex == 0"
                                    :src="$kt.file.byPath('image/icon/voiceInPlay0.png')" mode="widthFix" />
                                <image class="box-msg-content-val-voice-icon" v-if="voiceIconIndex == 1"
                                    :src="$kt.file.byPath('image/icon/voiceInPlay1.png')" mode="widthFix" />
                                <image class="box-msg-content-val-voice-icon" v-if="voiceIconIndex == 2"
                                    :src="$kt.file.byPath('image/icon/voice.svg')" mode="widthFix" />
                            </view>


                            <view class="box-msg-content-val-voice-time"
                                v-if="item.duration != null && item.duration != '' && !isMeSend(item)">
                                {{
                                    (item.duration / 1000).toFixed(0) }}{{ "\"" }}</view>

                        </view>

                    </view>
                </view>

                <!-- 头像 -->
                <text class="box-msg-read" v-if="!isMeSend(item) & !item.isRead">未读{{ read(item) }}</text>

                <!-- 头像 -->
                <avatar v-if="isMeSend(item)" :fileId="item.ovoUserOfSend.user.fileIdOfAvatar"></avatar>

            </view>

        </view>

        <view id="bottomInScroll" style="height: 20rpx;"></view>
    </scroll-view>
</template>


<script>
const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();

innerAudioContext.autoplay = true;

export default {
    props: {
        room: {
            type: Object,
            default: () => {
                return {}
            }
        },
        height: {
            type: String,
            default: "100%"
        }
    },
    data() {
        return {
            // 是否到底部
            isToBottom: false,
            // 是否显示刷新
            isRefresherTriggered: false,
            // 是否不能再刷新
            isRefresherEnabled: true,
            requestParam: {
                typeCode: "privateChat",
                roomId: "",
                otherUserId: "",
                pageNumber: 1
            },
            bodyData: {
                content: []
            },
            // 是否加载完毕
            isLoadOver: false,
            // 正在播放的语音的item
            playingVoiceItem: {
                content: ""
            },
            // 是否正在播放语音
            isPlayingVoice: false,
            // 语音图片显示的index
            voiceIconIndex: 0,
            // 语音图片显示的定时器
            voiceIconInterval: null,
            roomIdNumber: 0,
        }
    },
    mounted() {

        if (this.room.id != null) {
            uni.showLoading({
                title: '加载中'
            });
            this.roomIdNumber++;
            this.onRoomChat();
            this.requestParam.roomId = this.room.roomId;

            this.requestParam.typeCode = this.room.typeCode;
            if (this.room.typeCode == "privateChat") {
                this.requestParam.otherUserId = this.room.otherUser.userId;
            }


            this.getBodyData();
            return false;
        }

        setTimeout(() => {
            this.isToBottom = false;
        }, 100);

        setTimeout(() => {
            this.isToBottom = true;
        }, 1000);

    },
    methods: {
        read(item) {
            setTimeout(() => {
                console.log("read-------");
                if (item.isRead) {
                    return "";
                }
                item.isRead = true;
                // 删除未读消息记录
                this.$ovo.removeUnreadMessage(item);
            }, 500);
            return "";
        },

        voiceToWidth(item) {
            if (item.duration == null && item.duration == '') {
                return '90rpx';
            }
            if (item.duration < 1000) {
                this.item.duration = 1000;
            }
            if (item.duration > 60000) {
                this.item.duration = 60000;
            }

            if ((item.duration / 1000) < 3) {
                return '90rpx';
            }

            if ((item.duration / 1000) * 10 + 50 < 380) {
                return (item.duration / 1000) * 10 + 50 + "rpx";
            }

            return '380rpx';
        },
        /**
         * 监听聊天
         * @param {} item 
         */
        onRoomChat() {
            console.log("onRoomChat发起了监听");
            uni.$on("newChat:roomId:" + this.room.roomId, (data) => {

                // 判断是否有重复
                if (this.isRepeat(data)) {
                    return false;
                }


                // 如果是私聊，且不是我发的，就添加到bodyData
                if (this.requestParam.typeCode == "privateChat" && !this.isMeSend(data)) {
                    data.ovoUserOfSend = this.room.otherUser;
                    data.isMeSend = false;
                    this.toBottom();
                }
                if (this.requestParam.typeCode == "privateChat" && this.isMeSend(data)) {
                    data.ovoUserOfSend = this.$ovo.getSelf();
                    data.isMeSend = true;
                    this.toBottom();
                }
                if (this.$ovo.isUnreadMessage(data.id)) {
                    this.read(data);
                }
                data.isRead = true;
                this.bodyDataPush(data);
            });
        },
        playVoice(item) {
            if (this.playingVoiceItem.content == item.content) {
                this.isPlayingVoice = false;
                clearInterval(this.voiceIconInterval);
                innerAudioContext.stop();
                this.playingVoiceItem = {
                    content: ""
                };
                return false;
            }
            clearInterval(this.voiceIconInterval);
            this.isPlayingVoice = true;
            this.playingVoiceItem = item;
            innerAudioContext.stop();
            setTimeout(() => {
                innerAudioContext.src = this.$kt.file.visit(item.content);
                console.log(innerAudioContext.src);
                innerAudioContext.play();
                this.voiceIconInterval = setInterval(() => {
                    this.voiceIconIndex++;
                    if (this.voiceIconIndex > 2) {
                        this.voiceIconIndex = 0;
                    }
                }, 500);

                setTimeout(() => {
                    clearInterval(this.voiceIconInterval);
                    this.voiceIconIndex = 0;
                    this.isPlayingVoice = false;
                }, item.duration);

            }, 100);
        },
        /**
         * 前往底部
         */
        toBottom() {
            this.isToBottom = false;
            setTimeout(() => {
                this.isToBottom = true;
            }, 10);
        },
        refresherpulling() {
            if (this.isRefresherTriggered) {
                return false;
            }
            this.isRefresherTriggered = true;
            setTimeout(() => {
                this.isRefresherTriggered = false;
            }, 1000);
            if (!this.isRefresherEnabled) {
                return false;
            }
            this.findLess();
        },
        async findLess() {


            if (this.room.id == null || this.room.id == '') {
                return false;
            }

            this.requestParam.roomId = this.room.id;

            await new Promise((resolve, reject) => {
                this.$kt.request.request({
                    api: this.$api.business.ovo.chat.findLess,
                    data: {
                        roomId: this.requestParam.roomId,
                        id: this.bodyData.content[0].id
                    },
                    stateSuccess: (res) => {
                        var jsonArr = res.data.content;
                        if (jsonArr.length == 0) {
                            // 关闭刷新
                            this.isRefresherEnabled = false;
                            // 没有数据，不再请求
                            return false;
                        }
                        for (var i = 0; i <jsonArr.length ; i++) {
                            // 是否显示时间
                            if (i == 0) {
                                jsonArr[i].isShowTime = true;
                            } else {
                                jsonArr[i].isShowTime = jsonArr[i].gmtCreate - jsonArr[i - 1].gmtCreate > 5 * 60 * 1000;
                            }

                            if (this.$ovo.isUnreadMessage(jsonArr[i].id)) {
                                jsonArr[i].isRead = false;
                            } else {
                                jsonArr[i].isRead = true;
                            }

                            this.bodyDataAdd(jsonArr[i]);
                            if (!this.isLoadOver) {
                                this.toBottom();
                            }
                        }

                        if (!this.isLoadOver) {
                            uni.hideLoading();
                        }
                        this.isLoadOver = true;
                        resolve();

                    }
                });
            });
        },

        async getBodyData() {


            if (this.room.id == null || this.room.id == '') {
                return false;
            }

            this.requestParam.roomId = this.room.id;

            await new Promise((resolve, reject) => {
                this.$kt.request.request({
                    api: this.$api.business.ovo.chat.getByRoomId,
                    data: this.requestParam,
                    stateSuccess: (res) => {
                        var jsonArr = res.data.content;
                        if (jsonArr.length == 0) {
                            this.requestParam.pageNumber--;
                            // 关闭刷新
                            this.isRefresherEnabled = false;
                            // 没有数据，不再请求
                            return false;
                        }
                        // 添加分页，为了下一次请求
                        this.requestParam.pageNumber++;
                        for (var i = 0; i < jsonArr.length; i++) {
                            // 是否显示时间
                            if (i == 0) {
                                jsonArr[i].isShowTime = true;
                            } else {
                                jsonArr[i].isShowTime = jsonArr[i].gmtCreate - jsonArr[i - 1].gmtCreate > 5 * 60 * 1000;
                            }

                            if (this.$ovo.isUnreadMessage(jsonArr[i].id)) {
                                jsonArr[i].isRead = false;
                            } else {
                                jsonArr[i].isRead = true;
                            }

                            this.bodyDataAdd(jsonArr[i]);
                            if (!this.isLoadOver) {
                                this.toBottom();
                            }
                        }

                        if (!this.isLoadOver) {
                            uni.hideLoading();
                        }
                        this.isLoadOver = true;
                        resolve();

                    }
                });
            });
        },
        async bodyDataAdd(item) {

            // 是否是自己发送的，用于判断消息显示在左边还是右边
            item.isMeSend = this.isMeSend(item);

            await new Promise((resolve, reject) => {
                if (this.isLoadOver) {

                    setTimeout(() => {
                        // 判断是否有重复
                        if (this.isRepeat(item)) {
                            resolve();
                            return false;
                        }
                        this.bodyData.content.unshift(item);
                        resolve();
                    }, 200);

                } else {
                    this.bodyData.content.unshift(item);
                    this.toBottom();
                    resolve();
                }
            });
        },
        bodyDataPush(item) {
            console.log('bodyDataPush');
            this.bodyData.content.push(item);
        },
        isMeSend(item) {
            return item.userIdOfSend == this.$ovo.getSelf().userId;
        },
        // 判断新增的消息是否有重复
        isRepeat(item) {
            for (var i = 0; i < this.bodyData.content.length; i++) {
                if (this.bodyData.content[i].id == item.id) {
                    return true;
                }
            }
            return false;
        },
        /**
         * 查看图片
         */
        previewImage(url) {
            uni.previewImage({
                urls: [url],
                current: url
            });
        },
    },
    watch: {
        // 监听数据变化
        room: {
            handler: function (val, oldVal) {
                if (this.room.id != null && this.room.id != '') {
                    console.log("room.id:" + this.room.id);
                    if (this.roomIdNumber == 0) {
                        this.onRoomChat();
                        this.$forceUpdate();
                    }
                    // this.roomIdNumber++;
                }
            },
            deep: true,
            immediate: true
        }
    }
}
</script>

<style lang="scss" scoped>
.box-msg-read {
    text-align: center;
    font-size: 24rpx;
    color: #999;
}

.box-top {
    padding: 20rpx 20rpx 0rpx 20rpx;
    padding-bottom: 0rpx;
    box-sizing: border-box;
    background-color: #fff;
}

.box {
    padding: 20rpx;
    box-sizing: border-box;
    margin-bottom: 20rpx;
}


.box-msg-time {
    font-size: 24rpx;
    text-align: center;
    color: #999;
    margin-bottom: 10rpx;
}

.box-msg-content {
    display: inline-block;
    vertical-align: top;
    max-width: calc(100% - 200rpx);
    margin: 20rpx;

    .box-msg-content-nickname {
        font-size: 28rpx;
        color: #666;
        margin-bottom: 10rpx;
    }

    .box-msg-content-val {
        display: inline-block;
        background-color: #fff;
        padding: 15rpx;
        border-radius: 0 20rpx 20rpx 15rpx;
        text-align: left;
        font-size: 30rpx;
    }
}

.box-msg-content-val-voice-icon {
    width: 40rpx;
    height: 40rpx;
}

.box-msg-content-val-voice-time {
    display: inline-block;
    font-size: 26rpx;
    color: #999;
    vertical-align: top;
}

.box-msg-icon {
    width: 150rpx;
    height: 150rpx;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    // 颜色反转
    filter: invert(10%);
}
</style>